.App {
  text-align: center;
  background-color: white                                                                                                                                                                                                                                   ;
  font-family: Arial, Helvetica, sans-serif;
  
}

.App-logo {
  font-family: 'Rubik Vinyl', cursive;
  pointer-events: none;
  position: fixed;
  margin-left: 5vw;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 5s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {transform: rotateY(45deg);}
	to {transform: rotateY(225deg);}
}

.vertical-menu {
  width: 15vw; /* Set a width if you like */
  padding-left: 5vw;
  text-align: center;
  /* float: left; */
}

.Home {
text-align: center;
}

.center-content {
  /* background-color: rgba(242, 246, 250, 0.5); */
  width: 100%;
  position: fixed;
  padding: 5vw;
  text-align: left;
}

.projects {
  padding: 5vw;
}

.projects h1 {
  font-family: 'EB Garamond', serif;
}

a {
  color: black;
  text-decoration: none;
}

a:hover {
  color: navy;
}

@media only screen and (max-width: 768px) {
  .center-content{
    text-align: center;
    padding: none;
    position: relative;
  }
  .App-logo {
    font-family: 'Rubik Vinyl', cursive;
    pointer-events: none;
    position: relative;
    margin:0;
  }
  img .vw{
    width:250px;
    height:250px;
  }
}

